<template>
    <div class="setComponent">
        <div class="form-item">
            <label>标题</label>
            <el-button size="mini" @click="butClick(['title', obj.title])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>描述</label>
            <el-button size="mini" @click="butClick(['smTitle', obj.smTitle])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>背景图片</label>
            <el-button size="small" type="primary" @click="uploadClick">上传图片</el-button>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CoverImg',
    props: {
        obj: {
            type: Object,

        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        }
    }
}
</script>
