<template>
    <div class="setComponent">
        <div class="form-item">
            <label>标题</label>
            <el-button size="mini" @click="butClick(['title', obj.title])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>描述</label>
            <el-button size="mini" @click="butClick(['smTitle', obj.smTitle])">编辑</el-button>
        </div>
        <div class="form-item">
            <label>图片</label>
            <el-button size="small" type="primary" @click="uploadClick">上传图片</el-button>
        </div>
        <div class="form-item">
            <label>动画设置</label>
            <el-select v-model="obj.animateImg" clearable placeholder="请选择">
                <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                </el-option>
            </el-select>
        </div>
    </div>
</template>
<script>
export default {
    name: 'TextLeftImg',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: [
                {
                    label: 'slideInLeft',
                    value: 'slideInLeft'
                }
            ]
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('imgloadClick',this.obj)
        }
    }
}
</script>
