<template>
    <div-row :obj="obj" class="content-wrapper image-text">
        <div class="row">           
            <div class="col col-text" :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                <div>
                    <h2 class="text-title" v-html="obj.title"></h2>
                    <p class="text-desc" v-html="obj.smTitle">
                        
                    </p>
                </div>
            </div>
            <div class="col col-img" :class="obj.animateText? 'wow '+obj.animateText : ''">
                <span>
                    <img :src="obj.image">
                </span>
            </div>
        </div>
    </div-row>
</template>
<script>
import {WOW} from 'wowjs'
export default {
    name: 'TextRightImg',
    props: {
        obj: {
            type: Object
        }
    },
    mounted() {
        this.$nextTick(() => { // 在dom渲染完后,再执行动画
          var wow = new WOW({
            live: false
          })
          wow.init()
        })

    }
}
</script>
<style lang="less" scoped>

</style>



