<template>
    <div-row :obj="obj" class="content-wrapper text-picture">
        <div class="row">   
            <div class="col col-img" :class="obj.animateText? 'wow '+obj.animateText : ''">
                <span>
                    <img :src="obj.image" alt="img">
                </span>
            </div>        
            <div class="col col-text" :class="obj.animateText? 'wow '+obj.animateText : ''">
                <div>
                    <h2 class="text-title" v-html="obj.title"></h2>
                    <p class="text-desc" v-html="obj.smTitle">
                        
                    </p>
                </div>
            </div>
            
        </div>
    </div-row>
</template>
<script>
import {WOW} from 'wowjs'
export default {
    name: 'TextTopImg',
    props: {
        obj: {
            type: Object
        }
    },
    mounted() {
        this.$nextTick(() => { // 在dom渲染完后,再执行动画
          var wow = new WOW({
            live: false
          })
          wow.init()
        })

    }
}
</script>
<style lang="less" scoped>
    .content-wrapper{
        .row{
            padding: 30px 20px 15px 20px;
        }
    }
</style>



